@import url("https://fonts.googleapis.com/css?family=Roboto+Mono:300,400,500,700");

*{
  margin: 0;
  padding: 0;
}

body {
  /* color: #999999; */
  font-size: 14px;
  font-family: "Roboto Mono", monospace;
  line-height: 1.80857;
  
}

/* Navbar Styling */
.navbar li a{
  color: black;
} 


/* End Navbar Styling */


.header{
  padding: 20px 0px;
}

hr {
  border: solid 1px black;
  margin-bottom: 70px;
}

.about p{
  font-size: 20px;
}
.about{
  padding-bottom: 40px;
}

.section-title {
  text-align: center;
  margin: 15px 0px;
}

.avatar {
  display: block;
  border-radius: 50%;
  height: 190px;
  width: 190px;
  align-self: center;
  margin-left: auto;
  margin-right: auto;
}

.icons{
  display: block;
  text-align: center;
  padding-bottom: 10px;
}

.icons a{
font-size: 40px;
letter-spacing: 10px;

}

.icons i{
  color: black;
}

.btn-link{
  margin-top: 10px;
  display: block;
  text-align: center;
}

.col-md-4 .inner-box{
  padding: 15px;
  margin-bottom: 30px;
}

.col-md-4 .align-center {
  text-align: center;
}

.col-md-4 .border{
  border: 1px solid #dee2e6;
}

.card{
  width: 20rem;
}

.content{
  margin: 0 auto;
}

.submit-btn{
  display: block;
  margin: 0 auto;
}
.contact-form{
  margin: 30px 0px;
}

.project-title{
  text-align: center;
}
.project-hr{
  margin: 20px;
}

/* CONTACT FORM */
.contact-form{
  padding-bottom: 50px;
}
/* END CONTACT FORM */

  /* Footer Styling */
.footer-basic {
  padding: 40px 0;
  /* color: #ffffff;
  background-color: #4b4c4d; */
  color: white;
    background-color: #1B2430;
}

.footer-basic ul {
  padding: 0;
  list-style: none;
  text-align: center;
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 0;
}

.footer-basic li {
  padding: 0 10px;
}

.footer-basic ul a {
  color: inherit;
  text-decoration: none;
  opacity: 0.8;
}

.footer-basic ul a:hover {
  opacity: 1;
}

.footer-basic .social {
  text-align: center;
  padding-bottom: 25px;
}

.footer-basic .social>a {
  font-size: 24px;
  width: 50px;
  height: 50px;
  line-height: 40px;
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  border: 1px solid #ccc;
  margin: 0 8px;
  color: inherit;
  opacity: 0.75;
}

.footer-basic .social>a:hover {
  opacity: 0.9;
}

.footer-basic .copyright {
  margin-top: 15px;
  text-align: center;
  font-size: 13px;
  color: #aaa;
  
}

/* End Footer Styling */

